<template>
  <div >
    <div class="header">
       <el-button
          size="mini"
          type="primary"
          style="float: right;"
          @click="handleAdd()">{{$t('message.add_btn')}}</el-button>
    </div>
    <el-table
    :data="tableData"
    class="table"
    style="width: 90%">
    <el-table-column
      :label="$t('message.detail_table_title')"
      align="left"
      width="780">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.title }}</span>
      </template>
    </el-table-column>
    <el-table-column
      :label="$t('message.detail_table_langue')"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.language }}</span>
      </template>
    </el-table-column>
     <el-table-column
      :label="$t('message.detail_table_type')"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.menu }}</span>
      </template>
    </el-table-column>
        <el-table-column :label="$t('message.detail_table_opt')">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">{{$t('message.edit_btn')}}</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="deleteItem(scope.row.menu == '组件中心' ? 'component' : 'solution', scope.row.title)">{{$t('message.del_btn')}}</el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import Service from '@/services/http'
export default {
  data() {
  return {
     tableData: []
    }
  },
  async created () {
      this.getData()
  },
  methods: {
    async getData() {
        this.tableData = [];
        let res = await Service.getData();
        res = JSON.parse(res.data)
        res.Solution.menu.map((item) => {
            this.tableData.push(item)
        })
        res.Component.menu.map((item) => {
            this.tableData.push(item)
        })
    },
    handleEdit(index, scope){
        //this.$router.replace('/detail')
        this.$router.push(
        {
            path: '/detail',
            query: {
                title: scope.title,
                menu: scope.menu == '组件中心' ? 'Component' : 'Solution'
            }
        }
        )
    },
    handleAdd(){
      this.$router.replace('/detail')
    },
    async deleteItem (obj, title) {
      let res = await Service.getData();
      res = JSON.parse(res.data)
      if (obj == 'solution') {
        res.Solution.menu.map((item, index) => {
          if (item.title == title) {
            res.Solution.menu.splice(index, 1); 
          }
        })
      }
      if (obj == 'component') {
        res.Component.menu.map((item, index) => {
          if (item.title == title) {
            res.Component.menu.splice(index, 1); 
          }
        })
      }
      this.$confirm(this.$t('message.detail_form_model_title'), this.$t('message.detail_form_model_tips'), {
        confirmButtonText: this.$t('message.detail_form_model_determine'),
        cancelButtonText: this.$t('message.detail_form_model_cancel'),
        type: 'warning'
      }).then(async () => {
        let resSet = await Service.setData({data:JSON.stringify(res)});
        if (resSet.code && resSet.code == 200) {
            this.$emit('changeNum')
            this.$message({
                type: 'success',
                message: this.$t('message.detail_form_model_successfully_deleted')
            });
            this.getData()
        } else {
          this.$message({
            type: 'error',
            message: this.$t('message.detail_form_model_deletion_failed')
          });
        }
     
      }).catch(() => {
        this.$message({
          type: 'info',
          message: this.$t('message.detail_form_model_deletion_canceled')
        });          
      });
    },
  }
}
</script>
<style scoped>
 .table {
     margin: 0 auto;
     margin-top: 50px;
     min-width: 1420px
 }
 .header {
    padding: 15px 50px;
    background: #fff;
    border-bottom: 1px solid #dcd5d5;
    box-shadow: -5px 0 10px #dcd5d5;
    top: 0;
    height: 60px;
    position: sticky;
 }
</style>
